<template>
  <div id='joinup' class="section-join">
    <div class="title-event">
      <i>{{title}}</i>
    </div>
    <div class="container">
        <div class="row justify-content-center pt-4">
          <div class="col-6 col-sm-5 col-md-4 col-lg-3 col-xl-2 icon-join">
            <img src="/static/images/ISOTIPO-BASE.png" alt="">
          </div>
          <div class="col-12 col-sm-12 col-md-8 col-xl-10 pt-3">
            <div class="joinup-content p-3">
              <i class="title">{{titlejoin}}</i>
              <p>{{contentjoin}}</p>
            </div>
          </div>
        </div>
    </div>
    <div class="col-12 justify-content-center d-flex py-4 pb-5">
      <a class="btn btn-primary p-2 px-3 btn-join" :href="url">{{btnfanpage}}</a>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      title: 'UNETE A LA COMUNIDAD',
      titlejoin: 'Somos una comunidad de videojuegos de pokémon',
      contentjoin: 'Tenemos la misión de unir a todos los entrenadores de la ciudad Trujillo. Realizamos torneos oficiales, torneos temáticos, batallas amistosas y reuniones de confraternización. Nuestra meta es competir y jugar a nivel mundial en los torneo de Pokémon. ¡Trujillo puede! ¡Anímate maestro pokémon!',
      btnfanpage: 'Visita la fanpage',
      url: 'https://www.facebook.com/pg/PokemonTrujillo/events/?ref=page_internal'
    }
  }
}
</script>

<style lang="css">
</style>



