<template>
  <div id='eventos' class="section-events">
    <div class="title-event">
      <i>EVENTOS POKETRAINER</i>
    </div>
    <div class="container pt-5">
        <div class="row justify-content-center">
          <div class="col-10 col-sm-8 col-md-4 col-lg-4 icon-league">
            <img :src="icon" alt="">
          </div>
          <div class="col-12 col-sm-12 col-md-8 p-3">
            <div class="event-content p-3">
              <p>Ven y participa en los eventos que Poketrainers Trujillo tiene para ti, vive emocionantes batallas, conoce nuevos entrenadores y conviértete en el mejor!</p>
              <p>Comparte buenos momentos con amigos y disfruta de la experiencia de convertirte en un maestro Pokemon.</p>
            </div>
          </div>
        </div>
        <div class="justify-content-center row">
          <div class="col-12 col-sm-10 col-md-8 col-lg-6 section-logos">
            <span class="" v-for='img in images'>
              <img class='section-img' :src="img.pkmn" alt="">
            </span>
          </div>
        </div>
    </div>
    <div class="col-12 justify-content-center d-flex pb-5 pt-2">
      <a class="btn btn-primary p-3 btn-eventos" :href="url">{{textbtn}}</a>
    </div>
  </div>
</template>

<script>
import ptCard from './cardEvent'
export default {
  data: function () {
    return {
      icon: '/static/images/VGC2015.png',
      url: 'https://www.facebook.com/pg/PokemonTrujillo/events/?ref=page_internal',
      textbtn: 'Próximos eventos',
      ptcard: [
        {
          icon: '/static/images/icon.bmp',
          url: 'google.com',
          text: 'Dinamicas'
        },
        {
          icon: '/static/images/icon.bmp',
          url: 'google.com',
          text: 'Torneos'
        },
        {
          icon: '/static/images/icon.bmp',
          url: 'google.com',
          text: 'FAQs'
        }
      ],
      images: [
        {
          pkmn: '/static/images/Pokemon_moon_logo.png'
        },
        {
          pkmn: '/static/images/Pokemon_Sun_logo.png'
        }
      ]
    }
  },
  components: {
    ptCard
  }
}
</script>



