<template>
  <!-- esto lo trabajaremos mas adelante, asi que espera sentado >
  <div class="principal-slider container-fluid px-0">
    <img src="/static/images/slider_01.bmp" class="pktemporal" alt="">
  </div -->
  <div id="carouselExampleControls" class="slider-section carousel slide top-p" data-ride="carousel">
    <div class="carousel-inner" role="listbox">
      <div class="carousel-item active">
        slide #1
        <img class="d-block img-fluid" src="/static/images/slider_01.bmp">
      </div>
      <div class="carousel-item">
        slide #2
        <img class="d-block img-fluid" src="/static/images/slider_01.bmp">
      </div>
      <div class="carousel-item">
        slide #3
        <img class="d-block img-fluid" src="/static/images/slider_01.bmp">
      </div>
    </div>
    <a class="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
      <span class="carousel-control-prev-icon" aria-hidden="true"></span>
      <span class="sr-only">Previous</span>
    </a>
    <a class="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
      <span class="carousel-control-next-icon" aria-hidden="true"></span>
      <span class="sr-only">Next</span>
    </a>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      slides: null,
      interval: 5000,
      activeSlide: null,
      activeIndex: -1
    }
  },
  mounted: function () {
    this.slides = document.querySelectorAll('.carousel-item')
    this.changeNext()
  },
  methods: {
    changeNext: function () {
      let vm = this
      setInterval(() => {
        vm.activeSlide = document.querySelector('.carousel-item.active')
        vm.slides.forEach((slide, idx) => {
          if (slide === vm.activeSlide) {
            vm.activeIndex = idx
          }
        })
        let nextActiveIndex = vm.activeIndex === vm.slides.length - 1 ? 0 : vm.activeIndex + 1
        vm.activeSlide.classList.remove('active')
        vm.slides[nextActiveIndex].classList.add('active')
      }, vm.interval)
    }
  }
}
</script>

<style lang="scss">
</style>



